body {
  margin: 0;
  padding: 0;
  background-color: #bbb;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: auto;
  margin-top: 20px;
  width: 300px;
  background-color: #165eee;
  border: solid 1px black; }
  .App .App-header {
    color: #ff7;
    min-height: 10vh;
    font-size: calc(10px + 2vmin);
    border-bottom: solid 2px #ff7;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }

#display {
  margin: 5px;
  padding: 0 5px;
  background-color: #000;
  min-height: 8vh;
  font-size: 18px;
  display: flex;
  flex-direction: column;
  text-align: right;
  word-break: break-all; }

#formula {
  color: gold;
  flex-grow: 1; }

#input {
  flex-grow: 1;
  color: #e98410; }

.btn-container {
  padding: 5px;
  background-color: #0b58fd;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 40px;
  grid-template-areas: "seven eight nine plus" "four five six subtract" "one two three multiply" "zero zero decimal divide" "clear clear equals equals";
  grid-gap: 5px; }

#zero {
  grid-area: zero; }

#one {
  grid-area: one; }

#two {
  grid-area: two; }

#three {
  grid-area: three; }

#four {
  grid-area: four; }

#five {
  grid-area: five; }

#six {
  grid-area: six; }

#seven {
  grid-area: seven; }

#eight {
  grid-area: eight; }

#nine {
  grid-area: nine; }

#plus {
  grid-area: plus; }

#subtract {
  grid-area: subtract; }

#multiply {
  grid-area: multiply; }

#divide {
  grid-area: divide; }

#clear {
  grid-area: clear; }

#equals {
  grid-area: equals; }

#decimal {
  grid-area: decimal;
  font-size: 20px; }

#clear:hover {
  background-color: red; }

.btn {
  background-color: #bbb;
  color: #000;
  border-radius: 2px;
  font-size: 16px; }
  .btn:hover {
    background-color: #ffa35a; }

